@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --body-font: "Inter", sans-serif;
  --theme-bg: #1f1d2b;
  --body-color: #808191;
  --button-bg: #353340;
  --border-color: rgba(128, 129, 145, .24);
  --video-bg: #252936;
  --delay: 0s;
}

body {
  font-family: var(--body-font);
  color: var(--body-color);
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.section {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.container {
  max-width: 1280px;
  width: 100%;
  height: 100vh;
  background-color: var(--theme-bg);
  margin-left: auto;
  margin-right: auto;
  font-size: 15px;
  font-weight: 500;
  display: flex;
  position: relative;
  overflow: hidden;
}

.logo {
  width: 30px;
  height: 30px;
  color: #fff;
  background-color: #22b07d;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: none;
}

.logo-expand {
  color: #fff;
  font-size: 19px;
  font-weight: 600;
  line-height: 34px;
  text-decoration: none;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.logo-expand:before {
  content: "";
  background: var(--theme-bg);
  width: 200px;
  height: 70px;
  z-index: -1;
  position: absolute;
  top: -30px;
  left: 0;
}

.sidebar {
  width: 220px;
  height: 100%;
  flex-direction: column;
  flex-shrink: 0;
  padding: 30px;
  transition-duration: .2s;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.side-menu__item:hover .sidebar-link, .side-menu__item.is-active .sidebar-link {
  color: #fff;
  font-weight: 600;
}

.side-menu__item:hover:nth-child(2n+1) svg, .side-menu__item.is-active:nth-child(2n+1) svg {
  background: #ff7551;
}

.side-menu__item:hover:nth-child(2n) svg, .side-menu__item.is-active:nth-child(2n) svg {
  background: #32a7e2;
}

.side-menu__item:hover:nth-child(2n+3) svg, .side-menu__item.is-active:nth-child(2n+3) svg {
  background: #6c5ecf;
}

.sidebar.collapse {
  width: 90px;
  border-right: 1px solid var(--border-color);
}

.sidebar.collapse .logo-expand, .sidebar.collapse .side-title {
  display: none;
}

.sidebar.collapse .logo {
  display: flex;
}

.sidebar.collapse .side-wrapper {
  width: 30px;
}

.sidebar.collapse .side-menu svg {
  margin-right: 30px;
}

.side-menu {
  flex-direction: column;
  display: flex;
}

.side-menu a {
  color: var(--body-color);
  align-items: center;
  margin-top: 26px;
  text-decoration: none;
  display: flex;
}

.side-menu svg {
  width: 30px;
  background-color: var(--button-bg);
  border-radius: 10px;
  flex-shrink: 0;
  margin-right: 16px;
  padding: 8px;
}

.side-menu svg:hover {
  color: #fff;
}

.side-title {
  letter-spacing: .07em;
  color: #fff;
  margin-bottom: 24px;
  font-size: 12px;
}

.side-wrapper {
  border-bottom: 1px solid var(--border-color);
  width: 145px;
  padding: 36px 0;
}

.side-wrapper + .side-wrapper {
  border-bottom: none;
}

@keyframes bottom {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.anim {
  animation: bottom .8s var(--delay) both;
}

.main-container {
  flex-direction: column;
  flex-grow: 1;
  padding: 0 30px 30px;
  display: flex;
  overflow: auto;
}

.header {
  flex-shrink: 0;
  align-items: center;
  padding: 30px;
  display: flex;
}

.search-bar {
  height: 34px;
  width: 100%;
  max-width: 450px;
  display: flex;
}

.search-bar input {
  width: 100%;
  height: 100%;
  background-color: var(--button-bg);
  font-family: var(--body-font);
  color: #fff;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 56.966 56.966' fill='%23717790c7'%3e%3cpath d='M55.146 51.887L41.588 37.786A22.926 22.926 0 0046.984 23c0-12.682-10.318-23-23-23s-23 10.318-23 23 10.318 23 23 23c4.761 0 9.298-1.436 13.177-4.162l13.661 14.208c.571.593 1.339.92 2.162.92.779 0 1.518-.297 2.079-.837a3.004 3.004 0 00.083-4.242zM23.984 6c9.374 0 17 7.626 17 17s-7.626 17-17 17-17-7.626-17-17 7.626-17 17-17z'/%3e%3c/svg%3e");
  background-position: 96%;
  background-repeat: no-repeat;
  background-size: 14px;
  border: none;
  border-radius: 8px;
  padding: 0 40px 0 16px;
  font-size: 14px;
  font-weight: 500;
  box-shadow: 0 0 0 2px rgba(134, 140, 160, .02);
}

.user-settings {
  flex-shrink: 0;
  align-items: center;
  margin-left: auto;
  padding-left: 20px;
  display: flex;
}

.user-settings svg {
  width: 10px;
  flex-shrink: 0;
}

.user-img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
}

.user-name {
  color: #fff;
  margin: 0 6px 0 12px;
  font-size: 14px;
}

@media screen and (max-width: 767px) {
  .user-settings svg, .user-name {
    display: none;
  }
}

.main-header {
  color: #fff;
  z-index: 11;
  background: linear-gradient(#1f1d2b 0%, #1f1d2b 78%, rgba(31, 29, 43, 0) 100%);
  padding-bottom: 20px;
  font-size: 30px;
  font-weight: 700;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.most-watched {
  color: #fff;
  margin: 30px 0 20px;
  font-size: 24px;
  font-weight: 500;
}

.main-films {
  align-items: center;
  display: flex;
}

.main-film__rating {
  align-items: center;
  padding-bottom: 10px;
  display: flex;
  position: absolute;
  top: 200px;
  left: 20px;
}

.main-film__rating .tips {
  flex-direction: column-reverse;
  align-items: flex-start;
}

.main-film__title {
  max-width: 12ch;
  letter-spacing: 1px;
  color: #fff;
  margin-bottom: 30px;
  font-size: 25px;
  font-weight: 600;
  position: absolute;
  top: 30px;
  left: 20px;
}

.main-film {
  height: 280px;
  background-image: url("film-596009_1920.6b3de82b.jpg");
  background-position-x: 150px;
  background-repeat: no-repeat;
  background-size: 80%;
  border-radius: 20px;
  flex-direction: column;
  align-self: stretch;
  transition: background .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.main-film + .main-film {
  height: 280px;
  background-image: url("film-596009_1920.6b3de82b.jpg");
  background-color: unset;
  filter: saturate(1.4);
  background-position: center;
  background-size: cover;
  margin-left: 20px;
}

.video-poster__wrapper {
  object-fit: cover;
}

.video-poster__wrapper > img {
  object-fit: cover;
  object-position: top;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .main-film, .main-film + .main-film {
    width: 50%;
  }

  .main-film {
    background-blend-mode: overlay;
    background-position-x: center;
    background-size: cover;
  }
}

@media screen and (min-width: 1280px) {
  .main-film {
    width: 65%;
  }

  .main-film + .main-film {
    width: 35%;
  }
}

.rating__wrapper svg {
  color: #e7bb7d;
  border-color: #ffe6b2;
}

.film-detail {
  margin-left: 0;
}

.main-film__selection {
  color: #fff;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.rating-value {
  width: 52px;
  height: 52px;
  color: #fff;
  background-color: #081c22;
  border: 2px solid #0daabc;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 14px;
  padding: 4px;
  font-weight: 600;
  display: flex;
}

.rating__wrapper {
  flex-shrink: 0;
  position: relative;
}

.rating__wrapper svg {
  width: 16px;
  color: #0daabc;
  background-color: #fff;
  border: 2px solid #0daabc;
  border-radius: 50%;
  padding: 2px;
  position: absolute;
  bottom: 5px;
  right: 0;
}

.film-name {
  color: #fff;
  margin-bottom: 8px;
  font-size: 15px;
  font-weight: 500;
}

.film-info {
  color: #fff;
  font-size: 13px;
  font-weight: 400;
}

.film-detail {
  margin-left: 16px;
}

.seperate {
  width: 3px;
  height: 3px;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 50%;
  margin: 0 6px;
  display: inline-block;
}

.seperate.video-seperate {
  background-color: var(--body-color);
}

@media screen and (max-width: 767px) {
  .main-films {
    flex-wrap: wrap;
  }

  .main-film, .main-film + .main-film {
    width: 100%;
  }

  .videos {
    grid-template-columns: 1fr;
  }

  .main-film + .main-film {
    background-size: cover;
    margin-top: 20px;
    margin-left: 0;
  }
}

@media screen and (max-width: 475px) {
  .main-film__title {
    font-size: 20px;
  }

  .film-name {
    font-size: 14px;
  }

  .main-film__rating {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .film-detail {
    margin-left: 0;
  }

  .main-film .rating-value {
    margin-top: 14px;
  }

  .main-container {
    padding: 0 20px 20px;
  }

  .header {
    padding: 20px;
  }

  .sidebar.collapse {
    width: 40px;
  }

  .sidebar {
    align-items: center;
  }

  .container {
    height: 100vh;
    max-height: 100%;
  }
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(21, 20, 26, .63);
  border-radius: 10px;
}

.videos {
  width: 100%;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: repeat(1, 1fr);
  display: grid;
}

.video {
  background-color: var(--video-bg);
  border-radius: 20px;
  transition: transform 5s ease-in;
  position: relative;
  overflow: hidden;
}

.video-wrapper {
  height: 300px;
  position: relative;
}

.video-name {
  color: #fff;
  background-color: var(--video-bg);
  z-index: 9;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  padding: 25px 20px 0;
  font-size: 16px;
  line-height: 1.38;
  display: -webkit-box;
  position: relative;
  overflow: hidden;
}

.video-view {
  background-color: var(--video-bg);
  padding: 12px 20px 20px;
  font-size: 12px;
  position: relative;
}

.video-selection {
  color: rgba(255, 255, 255, .85);
  z-index: 1;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  top: 10px;
  right: 8px;
}

.video-poster {
  transition: transform .7s ease-in-out;
}

.video:hover .video-poster {
  transform-origin: center;
  transform: scale(1.3);
}

.video:hover .video-selection {
  display: none;
}

.video:hover .video-rating {
  z-index: 10;
  bottom: -65px;
  right: -3px;
  transform: scale(.6);
}

.video-rating {
  z-index: 99;
  transition: all .4s;
  position: absolute;
  bottom: -25px;
  right: 5px;
}

.video-rating svg {
  color: #fff;
  border-color: var(--video-bg);
  background-color: #0aa0f7;
}

@media screen and (min-width: 768px) {
  .videos {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1280px) {
  .videos {
    grid-template-columns: repeat(4, 1fr);
  }
}

footer {
  justify-content: center;
  display: flex;
}

.footer-svg {
  animation-name: anime;
  animation-duration: .3s;
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes anime {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.1);
  }
}

a.footer-link {
  text-decoration: underline;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  background-color: var(--theme-bg);
  width: 280px;
  max-height: 581px;
  border-radius: 20px;
  padding: 20px;
  transition: transform .25s ease-in-out;
  overflow-y: scroll;
  transform: scale(1.1);
}

.modal-close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: none;
  border: 1px solid rgba(255, 255, 255, .1);
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 2px;
  right: 2%;
}

.close-icon {
  fill: #fff;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.close-btn:hover .close-icon, .close-btn:focus .close-icon {
  fill: #ea5f5f;
}

@media screen and (min-width: 768px) {
  .modal {
    width: 700px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 800px;
  }
}

.title-team {
  text-align: center;
  color: #ea5f5f;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.15;
}

.team-list {
  text-align: center;
}

.our-team:not(:last-child) {
  margin-bottom: 10px;
}

.our-team {
  text-align: center;
  background-color: #252836;
  padding: 10px 0;
  position: relative;
  overflow: hidden;
}

.our-team .picture {
  height: 90px;
  width: 90px;
  z-index: 1;
  margin-bottom: 10px;
  display: inline-block;
  position: relative;
}

.our-team .picture:before {
  content: "";
  width: 100%;
  height: 0;
  opacity: .9;
  background-color: #ea5f5f;
  border-radius: 50%;
  transition: all .3s linear;
  position: absolute;
  bottom: 135%;
  left: 0;
  right: 0;
  transform: scale(3);
}

.our-team:hover .picture:before {
  height: 100%;
}

.our-team .picture:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #1369ce;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.our-team .picture img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  transition: all .9s;
  transform: scale(1);
}

.our-team:hover .picture img {
  transform: scale(.7);
  box-shadow: 0 0 0 14px #f7f5ec;
}

.our-team .title {
  color: #4e5052;
  text-transform: capitalize;
  font-size: 12px;
  display: block;
}

.our-team .social {
  width: 100%;
  background-color: #ea5f5f;
  margin: 0;
  padding: 0;
  transition: all .5s;
  position: absolute;
  bottom: -100px;
  left: 0;
}

.our-team:hover .social {
  bottom: 0;
}

.our-team .social {
  display: inline-block;
}

.our-team .social a {
  color: #fff;
  padding: 10px;
  font-size: 10px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.our-team .social a:hover {
  color: #ea5f5f;
}

@media screen and (min-width: 768px) {
  .team-list {
    flex-wrap: wrap;
    margin-top: -10px;
    margin-left: -10px;
    display: flex;
  }

  .team-list > .our-team {
    flex-basis: calc(50% - 10px);
    margin-top: 10px;
    margin-left: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .team-list > .our-team {
    flex-basis: calc(33.3333% - 10px);
  }
}

.backdrop {
  z-index: 12;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(128, 129, 145, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  box-sizing: border-box;
  width: 280px;
  min-height: 581px;
  color: #fff;
  background-color: #1f1d2b;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 12px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 700px;
    flex-direction: row;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 800px;
  }
}

.close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: none;
  border: 1px solid rgba(255, 255, 255, .1);
  border-radius: 50%;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close-icon {
  fill: #fff;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.close-btn:hover .close-icon, .close-btn:focus .close-icon {
  fill: #ea5f5f;
}

.modal_poster {
